<template>
  <div class="carousel">
    <div ref="slides" class="slides">
      <div class="slide" v-for="(image, index) in images" :key="index">
        <img class="img" :src="image" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: [
        require("../assets/img/p0.jpg"),
        require("../assets/img/p1.jpg"),
        require("../assets/img/p2.jpg"),
      ],
      currentIndex: 0,
      direction: true,
    };
  },
  mounted() {
    this.timer = setInterval(this.nextSlide, 2000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    nextSlide() {
      const match = this.$refs.slides.style.transform.match(/translateX\((-?\d+\.?\d*)px\)/);
      const translateX = match ? parseFloat(match[1]) : 0;

      if (translateX == 0) {
        this.direction = true;
        this.currentIndex = 0;
      } else if (translateX == -600) {
        this.direction = false;
        this.currentIndex = 6;
      }

      if (this.direction) {
        this.currentIndex ++;
      } else {
        this.currentIndex --;
      }
      this.$refs.slides.style.transform = `translateX(${-this.currentIndex * 100}px)`;
    },
  },
};
</script>

<style lang="less">
.carousel {
  margin-top: 20px;
  width: 100%;
  overflow: hidden;
  .slides {
    display: flex;
    transition: transform 0.5s ease;
    width: 300 * 3px;
    margin-left: 20px;
    .img {
      width: 300px;
      height: 0.8 * 240px;
      margin-right: 12px;
    }
  }
}
</style>